function AnimatedBar({ before, after, dark = false }: { before: number; after: number; dark?: boolean }) {
  const ref = useRef(null);
  const inView = useInView(ref, { once: true });

  return (
    <div ref={ref} className="space-y-4">
      <div className="flex items-center gap-4">
        <span className={`text-[9px] font-black uppercase tracking-widest w-14 flex-shrink-0 ${dark ? 'text-zinc-500' : 'text-zinc-400'}`}>
          BEFORE
        </span>
        <div className={`flex-1 h-2 ${dark ? 'bg-zinc-800' : 'bg-zinc-100'}`}>
          <motion.div
            className={`h-2 ${dark ? 'bg-zinc-600' : 'bg-zinc-300'}`}
            initial={{ width: '0%' }}
            animate={inView ? { width: `${before}%` } : { width: '0%' }}
            transition={{ duration: 1, ease: 'easeOut' }}
          />
        </div>
        <span className={`text-sm font-black w-10 flex-shrink-0 text-right ${dark ? 'text-zinc-400' : 'text-zinc-500'}`}>
          {before}%
        </span>
      </div>
      <div className="flex items-center gap-4">
        <span className="text-[9px] font-black uppercase tracking-widest w-14 flex-shrink-0 text-red-700">
          AFTER
        </span>
        <div className={`flex-1 h-2 ${dark ? 'bg-zinc-800' : 'bg-zinc-100'}`}>
          <motion.div
            className="h-2 bg-red-700"
            initial={{ width: '0%' }}
            animate={inView ? { width: `${after}%` } : { width: '0%' }}
            transition={{ duration: 1.2, ease: 'easeOut', delay: 0.3 }}
          />
        </div>
        <span className="text-sm font-black text-red-700 w-10 flex-shrink-0 text-right">
          {after}%
        </span>
      </div>
    </div>
  );
}

